<template>
  <div id="app">
    <router-view></router-view> 
  </div>
</template>

<script>

export default {
  name: 'app',
  data () {
    return {
      count: 0,
      firstName: 'Foo',
      lastName: 'Bar',
      fullName: ''
    }
  },
  computed: {
    datas () {
      return this.$store.state.items
    }
  },
  methods: {
    increment () {

    },
    changeTitle () {
      if (document.hidden) {
        document.title = '去吧, 皮卡丘'
      } else {
        document.title = '欢迎回来'
      }
    }
  },
  components: {

  },
  created: function () {
    document.addEventListener('visibilitychange', this.changeTitle, false)
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
}

html {
  background: #000;
}
html, body {

}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



