import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  items: [],
  name: ''
}

// 用于更改状态的mutation函数
const mutation = {
  addItem (state) {
    state.items.push({a: 1, b: 2})
  }
}

const action = {
  addItem ({ commit }) {
    commit('addItem')
  }
}

export default new Vuex.Store({
  state,
  mutation,
  action
})



